.cardDoc{
  width: 100vw;
  height: 100vh;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 5000;
  top: 0;
  left: 0;
}
.btn{
  color: rgb(255, 255, 255);
  position: absolute;
  top: 8%;
  right: 10%;
}
.cardContain{
  width: 45vw;
  height: 45vw;
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  transform-style: preserve-3d;
  backface-visibility: hidden;
}
.cardContain:hover > .front{
  transform: rotateY(-180deg);
}
.cardContain:hover > .back{
  transform: rotateY(0deg);
}
.front,.back{
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  background-color: white;
  position: absolute;
  display: flex;
  overflow: hidden;
  transition: transform 0.5s linear;
  backface-visibility: hidden;
}.front{
  transform: rotateY(0deg);
}
.back{
  transform: rotateY(180deg);
}.back h1 {
  font-size: 4rem;
  color: rgb(168, 10, 168);
}
.back p{
  color: Blue;
 padding: 10rem;
}

